@font-face {
  font-family: "Blair";
  src: local("Blair"), url("./media/Blaimim.ttf") format("truetype");
}
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;1,400&display=swap");

$mainRed: #d32228;
$champagne: #f5e2c8;
$aliceBlue: #e9f1f7;

.m-hide {
  @media (max-width: 768px) {
    display: none !important;
  }
}

.d-hide {
  @media (min-width: 769px) {
    display: none !important;
  }
}

body {
  background: white;
  overflow-x: hidden;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
body::-webkit-scrollbar {
  display: none;
}
.full-body {
  overflow-x: hidden !important;
}

.tbird-font {
  font-family: "Blair";
}

.playfair {
  font-family: "Playfair Display", serif;
}

.weight-400 {
  font-weight: 400;
}

.weight-500 {
  font-weight: 500;
}

.weight-600 {
  font-weight: 600;
}

.red {
  color: $mainRed;

  &:hover {
    text-decoration: none !important;
    color: $mainRed !important;
  }
}

.red-a {
  color: $mainRed !important;
}

.navbar {
  width: 100vw;
  background-color: rgba(255, 255, 255, 0);
  position: fixed;
  z-index: 5;

  .mobile-burger {
    .expandBurger {
      float: right;
    }
    .expansion {
      flex-direction: column;
    }
  }

  .navbar-logo {
    width: 10vw;
    max-width: 50px;
    cursor: pointer;
  }

  .navlink-container {
    float: right;

    a {
      cursor: pointer;
      color: black;
    }
    a:hover {
      color: white;
    }
  }
}

section {
  width: 100vw;
  min-height: 100vh;
}
.home {
  flex-direction: row;
  align-items: center;

  .home-abs {
    position: absolute;
    right: 0;
    top: 0;
    width: 50%;
    max-height: 100%;
    background-color: $mainRed;
    overflow: hidden;
    z-index: -1;

    img {
      width: 100%;
    }
  }

  .head-text {
    h1 {
      font-size: 5rem;
    }
    h2 {
      font-size: 4rem;
      letter-spacing: 0.1rem;
    }

    .playfair {
      font-size: 2rem;
      text-decoration: none !important;
      background-color: white;
      width: 75%;
    }
  }
}

.divider {
  max-width: 8vw;
}

.about {
  min-height: unset !important;
  span {
    font-family: "Playfair Display", serif !important;
  }
  p {
    font-size: 1.5rem;
    font-family: "Playfair Display", serif !important;
  }
}

.portfolio {
  min-height: unset !important;
  .bg {
    background-color: white;
  }
}
