@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{font-family:"Blair";src:local("Blair"),url(/static/media/Blaimim.7f0bd87d.ttf) format("truetype")}@media (max-width: 768px){.m-hide{display:none !important}}@media (min-width: 769px){.d-hide{display:none !important}}body{background:white;overflow-x:hidden;-ms-overflow-style:none;scrollbar-width:none}body::-webkit-scrollbar{display:none}.full-body{overflow-x:hidden !important}.tbird-font{font-family:"Blair"}.playfair{font-family:"Playfair Display", serif}.weight-400{font-weight:400}.weight-500{font-weight:500}.weight-600{font-weight:600}.red{color:#d32228}.red:hover{text-decoration:none !important;color:#d32228 !important}.red-a{color:#d32228 !important}.navbar{width:100vw;background-color:rgba(255,255,255,0);position:fixed;z-index:5}.navbar .mobile-burger .expandBurger{float:right}.navbar .mobile-burger .expansion{flex-direction:column}.navbar .navbar-logo{width:10vw;max-width:50px;cursor:pointer}.navbar .navlink-container{float:right}.navbar .navlink-container a{cursor:pointer;color:black}.navbar .navlink-container a:hover{color:white}section{width:100vw;min-height:100vh}.home{flex-direction:row;align-items:center}.home .home-abs{position:absolute;right:0;top:0;width:50%;max-height:100%;background-color:#d32228;overflow:hidden;z-index:-1}.home .home-abs img{width:100%}.home .head-text h1{font-size:5rem}.home .head-text h2{font-size:4rem;letter-spacing:0.1rem}.home .head-text .playfair{font-size:2rem;text-decoration:none !important;background-color:white;width:75%}.divider{max-width:8vw}.about{min-height:unset !important}.about span{font-family:"Playfair Display", serif !important}.about p{font-size:1.5rem;font-family:"Playfair Display", serif !important}.portfolio{min-height:unset !important}.portfolio .bg{background-color:white}

